<template>
  <div id="box">
    <!-- <header>
        <div class="back">

        </div>
        <div class="header-title">
            {{title}}
        </div>
        <div class="header-more"></div>
    </header> -->
    <div class="content">
        <div class="section">
            <div class="item noneBor">
                <label for="activityTitle">
                    活动标题<span>*</span>
                </label>
                <span>
                    <input :disabled="Type==3?true:false" type="text" v-validate="'required'" name="activityTitle" id="activityTitle" placeholder="请输入活动标题" v-model="activityTitle"/>
                    <i class="tip" v-show="errors.has('activityTitle')">{{ errors.first('activityTitle') }}</i>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item">
                <label>
                    活动封面<span>*</span>
                </label>
                <span class="flex-box">
                    <div class="goods-img" v-for="(item,i) in activityImg" :key="i">
                        <!-- <i :class="activityImgIndex==i?'true':''" :data-id="item.id"></i> -->
                        <img :src="item.dataurl==''?item.url:item.dataurl" :alt="item.alt" @click="Type==3?rt():previewImg(item,i)">
                    </div>
                    <div v-if="Type==3?false:true" class="goods-img">
                        <img src="../assets/icon-upimg.png" @click="upFileInfo.key=true">
                        <span style="font-size:14px;color:red;">建议上传正方形图片</span>
                    </div>
                </span>
            </div>
            <!-- <div class="item">
                <label>
                    活动附属图
                </label>
                <span>
                    <div class="flex-box">
                        <template v-for="(item,i) in activityInfo" v-if="Type==3?false:true">
                            <div class="goods-img" >
                                <img :src="item.url" :alt="item.alt" @click="previewImg1(item,i)">
                            </div>
                        </template>
                        <div v-if="Type==3?false:true" class="goods-img" @click="upFileInfo1.key=true">
                            <img src="../assets/icon-upimg.png">
                        </div>
                    </div>
                </span>
            </div> -->
            <div class="item nb">
                <label>
                    活动时间<span>*</span>
                </label>
                <span class="goods-fill-in start">
                    <div class="fle">
                        <button @click="Type==3?rt():startTime()" :data-date="st">{{starttime}}</button>
                    </div>
                    <div class="slimw fle">
                        至
                    </div>
                    <div class="fle">
                        <button class="right" @click="Type==3?rt():endTime()" :data-date="et">{{endtime}}</button>
                    </div>
                    <i class="btomr"></i>
                </span>
            </div>
        </div>
        <div class="section">
             <div class="item">
                <label for="activityMsg">
                    活动描述<span>*</span>
                </label>
                <span>
                    <textarea :disabled="Type==3?true:false" v-validate="'required|max:300'" name="activityMsg"  id="activityMsg" placeholder="请输入活动描述" cols="30" rows="3" v-model="activityMsg"></textarea>
                    <i class="tip" v-show="errors.has('activityMsg')">{{ errors.first('activityMsg') }}</i>
                </span>
            </div>
            <div class="item">
                <label>
                    活动详情<span>*</span>
                </label>
                <span>
                    <div>
                        <vue-editor :disabled="Type==3?true:false" @imageAdded="handleImageAdded" useCustomImageHandler v-model="content" :editorToolbar="toolbarOptions"></vue-editor>
                    </div>
                </span>
            </div>
        </div>
        <div class="section">
            <div class="item submintItem">
                <button v-if="Type==1" class="button" @click="ok">确定</button>
                <button v-if="Type==2" class="button" @click="ok">保存</button>
            </div>
        </div>
    </div>
    <image-html5-upload @imgIndex="changeIndex" imgNumLimit="1" :init="upFileInfo" v-if="upFileInfo.key" :parentImgArr="activityImg"></image-html5-upload>
    <!-- <upfile :init="upFileInfo" :imgArr="activityImg" @imgIndex="changeIndex" :addType="1"></upfile> -->
    <!-- 封面图片 -->
    <alert-img :init="imgPreview" :imgArr="activityImg" @indexChange="indexChange" @remove="removeImg" :autobutton="false"></alert-img>
    <!-- 开始时间 -->
    <mt-datetime-picker
        ref="date"
        type="date"
        :startDate="new Date('2010-01-01')"
        :endDate="endDate"
        v-model="startTime1"
        @confirm="handStrat"
        >
    </mt-datetime-picker>
    <!-- 结束时间 -->
    <mt-datetime-picker
        ref="date1"
        type="date"
        :endDate="new Date('2050-01-01')"
        :startDate="startDate"
        v-model="endTime1"
        @confirm="handEnd"
        >
    </mt-datetime-picker>
  </div>
</template>
<script>
    //import AlertImg from './component/AlertImg.vue';
    let AlertImg = resolve => {
        require.ensure(['./component/AlertImg.vue'], () => {
            resolve(require('./component/AlertImg.vue'))
        })
    }
    //import Upfile from './component/Upfile.vue';
    // let Upfile = resolve => {
    //     require.ensure(['./component/Upfile.vue'], () => {
    //         resolve(require('./component/Upfile.vue'))
    //     })
    // }
    let h5FileUpVue = resolve => {
        require.ensure(['./component/h5-file-up/h5-file-up.vue'], () => {
            resolve(require('./component/h5-file-up/h5-file-up.vue'))
        })
    }
    import { VueEditor } from 'vue2-editor';
    import { Validator } from 'vee-validate';
    import com from '../assets/common.js';
    import Exotic from '../config.js'
    export default{
        created(){
            let that=this;
            const dictionary = {
                zh_CN: {
                    messages: {
                        email: () => '邮箱格式不正确哦',
                        required:(field)=>field+"不能为空",
                        numeric:(field)=>field+"不是数字"
                    },
                    attributes:{
                        activityTitle:'活动标题',
                        activityMsg:'活动描述'
                    }
                }
            };
            Validator.localize(dictionary);
            if(this.$route.query.type!=undefined){
                this.Type=parseInt(this.$route.query.type);
            }
            if(this.$route.query.id!=undefined){
                this.id=parseInt(this.$route.query.id);
            }
            if(this.$route.query.taid!=undefined){
                this.taid=parseInt(this.$route.query.taid);
            }
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            if(this.Type==2||this.Type==3){
                if(this.Type==2){
                    this.title="修改活动"
                }else if(this.Type==3){
                    this.title="查看活动"
                }
                that.MintUi.Indicator.open({
                    text: '加载中...',
                    spinnerType: 'fading-circle'
                });
                Exotic.ajaxData(that.token,Exotic.urlMap.activity.detail, {activityId: that.id}, 'get',function(data){
                    //开始时间
                    that.st=data.activity.startTime;
                    that.starttime=com.getTimes(data.activity.startTime+'','YY-MM-DD');
                    that.startTime1=new Date(data.activity.startTime);
                    //结束时间
                    that.et=data.activity.endTime;
                    that.endtime=com.getTimes(data.activity.endTime+'','YY-MM-DD');
                    that.endTime1=new Date(data.activity.endTime);
                    that.activityTitle=data.activity.title;  //活动标题
                    //封面图片
                    that.imgPreview.id=new Date().getTime();
                    that.imgPreview.url=Exotic.imageUrl + data.activity.imgUrl//图片路径
                    that.imgPreview.index=0;
                    that.activityImg.push({
                        id:that.imgPreview.id,
                        alt:'说明',
                        dataurl:'',
                        url:Exotic.imageUrl + data.activity.imgUrl
                    });
                    that.activityMsg=data.activity.description;  //活动描述
                    //$("#a_link").val(data.activity.url);   //跳转链接
                    that.content=data.activity.content;//活动内容
                     that.MintUi.Indicator.close();
                });
            }
        },
        components:{
            AlertImg,
            // Upfile,
            VueEditor,
            'image-html5-upload':h5FileUpVue
        },
        data(){
            return {
                token:null,
                Type:1, //编辑类型
                id:null,//活动ID
                taid:null,//商圈ID
                title:'添加活动',
                toolbarOptions:[
                    ['bold', 'italic', 'underline', 'strike'],
                    //['blockquote',{ 'list': 'ordered'},{ 'list': 'bullet' },{ 'script': 'sub'}, { 'script': 'super' }],
                    [{ 'align': [] },'image'],
                    //[{ 'indent': '-1'}, { 'indent': '+1' },{ 'color': [] }, { 'background': [] },'clean'],          
                    //[{ 'font': [] }],
                    //[{ 'header': [1, 2, 3, 4, 5, 6, false] }]
                ],
                activityTitle:'',//活动标题
                activityMsg:'',//活动描述
                activityInfoType:false,
                activityImgIndex:0,//商品图片的默认选中索引
                activityInfoIndex:0,//商品详情默认选中索引
                startDate:new Date('2010-01-01'),//最小日期
                endDate:new Date('2050-01-01'),//最大日期
                content:'',
                st:'',
                et:'',
                starttime:'开始时间',
                endtime:'结束时间',
                startTime1:new Date(),
                endTime1:new Date(),
                //封面图片视图
                imgPreview:{
                    key:false,
                    index:0,
                    id:'',
                    url:''
                },
                //封面图片列表
                activityImg:[],
                //文件上传
                upFileInfo:{
                    key:false,
                    id:'123'
                },
                upFileInfo1:{
                    key:false,
                    id:'123'
                },
                //活动详情视图
                activityInfoPreview:{
                    key:false,
                    index:0,
                    id:'',
                    url:''
                },
                //活动详情
                activityInfo:[]
            }
        },
        methods:{
            //富文本图片上传
            handleImageAdded(file, Editor, cursorLocation){
                console.log('富文本图片：');
                console.log(file);
                let size = Math.floor(file.size / 1024);
                if(size>=5120){
                    Exotic.tip({txt:"图片大于5m"});
                    return false;
                }else{
                    //图片上传
                    Exotic.uploadFile(file,function(res){
                        let url = res.url // Get url from response
                        Editor.insertEmbed(cursorLocation, 'image', Exotic.imageUrl+url);
                    })
                }
            },
            //开始时间
            startTime(){
                this.$refs.date.open();
            },
            //结束时间
            endTime(){
                this.$refs.date1.open();
            },
            //开始时间确认
            handStrat(res){
                console.log(typeof res);
                this.starttime=com.getTimes(res,'YY-MM-DD');
                this.startDate=res;
                this.st=new Date(res).getTime();
            },
            //结束时间确认
            handEnd(res){
                this.endtime=com.getTimes(res,'YY-MM-DD');
                this.endDate=res;
                this.et=new Date(res).getTime();
            },
            //设置封面
            activityImgFn(ev,i){
                //console.log(ev);
                //this.activityImgIndex=i;
            },
            //预览图片
            previewImg(item,i){
                this.imgPreview.key=true;
                this.imgPreview.index=i;
                this.imgPreview.id=item.id;
            },
            //预览商品详情图片
            previewImg1(item,i){
                this.activityInfoPreview.key=true;
                this.activityInfoPreview.index=i;
                this.activityInfoPreview.id=item.id;
            },
            //设置封面返回
            indexChange(i){
                //设置的索引
                this.activityImgIndex=i;
                //数据变更
                this.imgPreview.id=this.activityImg[i].id;
                this.imgPreview.url=this.activityImg[i].url;
                this.imgPreview.index=parseInt(i);
            },
            //设置商品详情返回
            indexChange1(i){
                //设置的索引
                this.activityInfoIndex=i;
                //数据变更
                this.activityInfoPreview.id=this.activityInfo[i].id;
                this.activityInfoPreview.url=this.activityInfo[i].url;
                this.activityInfoPreview.index=parseInt(i);
            },
            //弹出防止冒泡
            rt(){
                return true;
            },
            //删除图片返回
            removeImg(){
                this.imgPreview.id='';
                this.imgPreview.url='';
                this.imgPreview.index=0;
                this.activityImgIndex=0;
            },
            //删除活动详情返回
            removeImg1(){
                this.activityInfoPreview.id='';
                this.activityInfoPreview.url='';
                this.activityInfoPreview.index=0;
                this.activityInfoIndex=0;
            },
            //上传封面图片返回
            changeIndex(obj){
                //默认图片索引
                this.activityImgIndex=obj.index;
                //imgPreview数据
                this.imgPreview.index=obj.index;
                this.imgPreview.id=this.activityImg[obj.index].id;
                this.imgPreview.url=this.activityImg[obj.index].url;
            },
            //上传活动详情返回
            changeIndex1(obj){
                //默认图片索引
                this.activityInfoIndex=obj.index;
                //activityInfoPreview数据
                this.activityInfoPreview.index=obj.index;
                this.activityInfoPreview.id=this.activityInfo[obj.index].id;
                this.activityInfoPreview.url=this.activityInfo[obj.index].url;
            },
            //运算找出对应ID的数据
            stext(arr,obj){
                /*return arr.find((item)=>{
                    return item.value == obj.value;
                });*/
                for(let i=0;i<arr.length;i++){
                    if(arr[i].value==obj.value){
                        return arr[i];
                    }
                }
            },
            ok(){
                let that=this;
                console.log(this.$data);
                //判断内容是否为空
                if(that.activityTitle == '') {
                    Exotic.tip({txt:"请填写活动标题"});
                    return false;
                }else if(that.imgPreview.url == '') {
                    Exotic.tip({txt:"请上传封面图片"});
                    return false;
                }else if(that.st == '') {
                    Exotic.tip({txt:"请选择开始时间"});
                    return false;
                }else if(that.et == '') {
                    Exotic.tip({txt:"请选择结束时间"});
                    return false;
                }else if(that.activityMsg == '') {
                    Exotic.tip({txt:"请填写活动描述"});
                    return false;
                }else if(that.content== ''){
                    Exotic.tip({txt:"请填写活动内容"});
                    return false;
                }else {
                    var editorHtml = that.content;
                    console.log('--详情--')
                    var regStr = /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/ig;
                    editorHtml = editorHtml.replace(regStr, "");
                    console.log(JSON.stringify(editorHtml))
                    if(that.Type==1){
                        var param = {
                            tradeAreaId: that.taid,//商圈ID
                            activityType: 1,  //活动类型 Integer  （1-店铺  2-商圈）
                            title: that.activityTitle,//标题
                            imgUrl: that.imgPreview.url.replace(Exotic.imageUrl,''),//封面
                            description: that.activityMsg,//描述
                            content: editorHtml,//详情
                            startTime: that.starttime,//开始时间
                            endTime: that.endtime  //结束时间
                        }
                        that.MintUi.Indicator.open({
                            text: '提交中...',
                            spinnerType: 'fading-circle'
                        });
                        Exotic.ajaxData(that.token,Exotic.urlMap.activity.add,param , 'post',function (data) {
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"添加活动成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=11";
                            }
                        });
                    }else if(that.Type==2){
                        var param = {
                            activityId: that.id,
                            tradeAreaId: that.taid,//商圈ID
                            activityType: 1,  //活动类型 Integer  （1-店铺  2-商圈）
                            title: that.activityTitle,//标题
                            imgUrl: that.imgPreview.url.replace(Exotic.imageUrl,''),//封面
                            description: that.activityMsg,//描述
                            content: editorHtml,//详情
                            startTime: that.starttime,//开始时间
                            endTime: that.endtime  //结束时间
                        }
                        that.MintUi.Indicator.open({
                            text: '提交中...',
                            spinnerType: 'fading-circle'
                        });
                        Exotic.ajaxData(that.token,Exotic.urlMap.activity.update,param , 'post',function (data) {
                            that.MintUi.Indicator.close();
                            Exotic.tip({txt:"修改活动成功"});
                            var u = navigator.userAgent;
                            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
                            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
                            if(isiOS){
                                //点击保存按钮的时候传递值返回列表
                                window.location.href = "#SuccessToPrevPage*arg=0";
                            }else if(isAndroid){
                                window.location.href = "fenzu://jscome/backToPrevPage?update=11";
                            }
                        });
                    }
                }
            }
        },
        mounted(){
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
</style>




